<div class="app" id="app"><div class="youtube-app-container"><youtube-controller class="controller"></youtube-controller><v-switch class="switch-style" v-model="isCurrentPlaylistOn" :label="isCurrentPlaylistOn ? 'Current Playlist Display: On' : 'Current Playlist Display: Off'" :class="{activated: isCurrentPlaylistOn}"></v-switch><v-switch class="switch-style" v-model="isPlaylistsMenuOn" :label="isPlaylistsMenuOn ? 'Saved Playlist Display: On' : 'Saved Playlist Display: Off'" :class="{activated: isPlaylistsMenuOn}"></v-switch><h3>Current Video: {{ currentVideo.name }}</h3><youtube-video class="video" :videoId="videoId"></youtube-video><current-playlist-menu :isCurrentPlaylistOn="isCurrentPlaylistOn"></current-playlist-menu><playlists-menu :isPlaylistsMenuOn="isPlaylistsMenuOn"></playlists-menu></div></div>