<div class="youtube-configurator"><div class="input-container"><v-text-field class="input-style" v-model="userInput" label="Enter URL or video ID" @keyup.enter="processAction(controllerOptions.PLAY_NOW)" color="purple" clearable hide-details="auto"></v-text-field><div class="controller-buttons"><v-btn class="add-style btn-style" @click="processAction(controllerOptions.PLAY_NOW)">Play now</v-btn><v-btn class="add-style btn-style" @click="onLoadPlaylist">Load Playlist</v-btn><v-btn class="add-style btn-style" @click="processAction(controllerOptions.QUEUE)">Queue Song</v-btn></div></div><v-switch class="switch-style" v-model="autopause" :label="autopause ? 'Autopause On' : 'Autopause Off'" :class="{activated: autopause}" @click="onSwitchClick"></v-switch><v-btn class="btn-style" v-if="devMode" @click="onSyncPlaylist">Sync Playlist</v-btn><div class="play-buttons"><v-btn class="btn-style" @click="onGoBack" :disabled="isCurrentPlaylistEmpty">Play Previous</v-btn><v-btn class="btn-style" @click="onPlayNext" :disabled="isCurrentPlaylistEmpty">Play Next</v-btn></div></div>