<div class="playlist-list"><div class="playlists" v-for="(playlist, playlistIndex) in playlists"><div class="name"><v-divider v-if="playlistIndex !== 0"></v-divider><h3>{{ playlist.name }}</h3></div><div class="controls"><v-chip @click="onEdit(playlistIndex)" color="red">Edit</v-chip><v-btn class="btn-style" @click="onLoad(playlistIndex)">Load</v-btn></div></div><playlist-editor :showModal="showEditModal" :editMode="true" :playlistId="selectedPlaylistId" @on-close-modal="onCloseModal"></playlist-editor></div>