<div class="song-list"><div class="songs" v-for="(song, id) in currentPlaylist"><div class="song"><v-divider v-if="id !== 0"></v-divider><div class="name" :class="{current: currentVideo.videoId === song.videoId}">{{ song.name }}</div></div><div class="controls"><v-chip @click="onDelete(song)" color="red">X</v-chip><v-btn class="btn-style" @click="onPlay(song)">Play</v-btn></div></div></div>