<div class="playlist-editor"><v-dialog width="500" v-model="isActive"><v-card title="Edit Playlist"><v-text-field label="Name" v-model="name"></v-text-field><v-card-actions><v-btn v-if="editMode" text="Delete" color="red" @click="onDelete"></v-btn><v-btn :text="confirmString" color="blue" @click="onUpdate" :disabled="isEmptyString"></v-btn><v-btn text="Close" @click="onClose"></v-btn></v-card-actions></v-card></v-dialog><v-dialog width="500" v-model="showConfirmDelete"><v-card title="Confirm Delete"><v-card-text>Are you sure you want to delete this playlist?</v-card-text><v-card-actions><v-btn text="Delete" @click="onConfirmDelete" color="red"></v-btn><v-btn text="Cancel" @click="onCancelDelete"></v-btn></v-card-actions></v-card></v-dialog></div>